//
// validation
// --------------------------------------------------
// 模块：表单验证
// 描述：表单验证模块处理
// --------------------------------------------------
// bootstrap 默认Less
@import "bootstrap/variables.less";
@import "bootstrap/mixins/forms.less";
@import "bootstrap/mixins/vendor-prefixes.less";
// 思诺博UI Less
@import "sinoui-variables.less";    // 变量

span.error,
.has-error .error,
.sino-form .sino-form-group .has-error .error {
    padding-left: 20px;
    background: url("../images/unchecked.gif") no-repeat left center;
}
span.error {
    margin-top: 5px;
    display: block;
    background-position: left top;
}
.has-success .success,
.sino-form .sino-form-group .has-success .success {
    background:url("../images/checked.gif") no-repeat left center;
    padding-left: 20px;
}
.sino-form .sino-form-group {
    &.error-block {
        padding-bottom: 25px;
    }
    .has-success {
        .form-control-validation(@input-color; @input-border; @input-group-addon-bg);
    }
    .has-warning {
        .form-control-validation(@input-color; @input-border; @input-bg);
    }
    .has-error {
        position: relative;
        .form-control-validation(@input-color; @input-border; @input-group-addon-bg);
        &.bootstrap-select {
            margin-bottom: 25px;
            .dropdown-toggle {
                border-color: @input-border;
            }
        }
        &.bootstrap-select .error.help-block,
        &.bootstrap-select span.input-group-addon + .error.help-block,
        .input-addon-edit + .error.help-block,
        .input-addon-edit + span.input-group-addon + .error.help-block,
        .input-addon-calendar + .error.help-block,
        .input-addon-calendar + span.input-group-addon + .error.help-block {
            position: absolute;
            left: 0;
            bottom: -26px;
            border: 0;
            width: 100%;
            text-align: left;
        }
        .error {
            background-color: transparent;
            color: @state-danger-text;
            text-align: left;
        }
        .bootstrap-select .error.help-block {
            bottom: -23px;
        }
        &.radio,
        &.checkbox {
            span.error {
                display: block;
            }
        }
    }
    .input-group {
        &.has-success,
        &.has-error {
            .input-group-addon.error.help-block {
                +.input-group-addon {
                    border-left-color: transparent;
                }
            }
            .input-addon-edit + .error.help-block ~ .input-group-addon,
            .input-addon-calendar + .error.help-block ~ .input-group-addon {
                background-color: @input-bg-disabled;
            }
            .input-addon-calendar,
            .input-addon-edit {
                +.input-group-addon {
                    border-left-color: transparent;
                }
            }
        }
    }
    .bootstrap-select {
        .btn {
            line-height: 1.4;
        }
    }
}
